.goods {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods .menu-wrapper {
    -ms-flex: 0 0 80px;
        flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
      display: table;
      padding: 0 12px;
      height: 54px;
      width: 56px;
      line-height: 14px;
}
.goods .menu-wrapper .menu-item.current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        font-weight: 700;
        background: #fff;
}
.goods .menu-wrapper .menu-item.current .text:after {
          display: none;
}
.goods .menu-wrapper .menu-item .icon {
        display: inline-block;
        vertical-align: top;
        width: 12px;
        height: 12px;
        margin-right: 2px;
        background-size: 12px 12px;
        background-repeat: no-repeat;
}
.goods .menu-wrapper .menu-item .icon.decrease {
          background-image: url("decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .icon.decrease {
              background-image: url("decrease_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .icon.discount {
          background-image: url("discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .icon.discount {
              background-image: url("discount_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .icon.guarantee {
          background-image: url("guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .icon.guarantee {
              background-image: url("guarantee_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .icon.invoice {
          background-image: url("invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .icon.invoice {
              background-image: url("invoice_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .icon.special {
          background-image: url("special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .icon.special {
              background-image: url("special_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .text {
        display: table-cell;
        font-size: 12px;
        width: 56px;
        vertical-align: middle;
        position: relative;
}
.goods .menu-wrapper .menu-item .text:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: '';
}
.goods .foods-wrapper {
    -ms-flex: 1;
        flex: 1;
}
.goods .foods-wrapper .title {
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: #93999f;
      background: #f3f5f7;
}
.goods .foods-wrapper .food-item {
      display: -ms-flexbox;
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      position: relative;
}
.goods .foods-wrapper .food-item:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        content: '';
}
.goods .foods-wrapper .food-item:last-child {
        margin-bottom: 0;
}
.goods .foods-wrapper .food-item:last-child:after {
          display: none;
}
.goods .foods-wrapper .food-item .icon {
        -ms-flex: 0 0 57px;
            flex: 0 0 57px;
        margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
