.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .seller-content .overview {
    position: relative;
    padding: 18px;
}
.seller .seller-content .overview .title {
      margin-bottom: 8px;
      line-height: 14px;
      color: #07111b;
      font-size: 14px;
}
