          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
}
.header .content-wrapper .content .description {
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
}
.header .content-wrapper .content .support .icon {
