.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  transform: translate3d(0, 0, 0);
}
.food .food-content .image-header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.food .food-content .image-header img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.food .food-content .image-header .back {
      position: absolute;
      top: 10px;
      left: 0;
}
.food .food-content .image-header .back .icon-arrow_lift {
        display: block;
        padding: 10px;
        color: #fff;
        font-size: 20px;
}
.food .food-content .content {
    position: relative;
    padding: 18px;
}
.food .food-content .content .title {
      margin-bottom: 8px;
      font-size: 14px;
      line-height: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .food-content .content .detail {
      margin-bottom: 18px;
      line-height: 10px;
      height: 10px;
      font-size: 0;
}
.food .food-content .content .detail .sell-count, .food .food-content .content .detail .rating {
        font-size: 10px;
        color: #93999f;
}
.food .food-content .content .detail .sell-count {
        margin-right: 12px;
}
.food .food-content .content .price {
      font-weight: 700;
      line-height: 24px;
}
.food .food-content .content .price .now {
        margin-right: 8px;
        font-size: 14px;
        color: #f01414;
}
.food .food-content .content .price .old {
        text-decoration: line-through;
        font-size: 10px;
        color: #93999f;
}
.food .food-content .content .cartcontrol-wrapper {
      position: absolute;
      right: 12px;
      bottom: 12px;
}
.food .food-content .content .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      height: 24px;
      line-height: 24px;
      padding: 0 12px;
      box-sizing: border-box;
      font-size: 10px;
      border-radius: 12px;
      color: #fff;
      background: #00a0dc;
}
.food .food-content .content .buy.fade-enter-active, .food .food-content .content .buy.fade-leave-active {
        transition: all 0.2s;
}
.food .food-content .content .buy.fade-enter, .food .food-content .content .buy.fade-leave-active {
        opacity: 0;
}
.food .food-content .info {
    padding: 18px;
}
.food .food-content .info .title {
      line-height: 14px;
      margin-bottom: 6px;
      font-size: 14px;
      color: #07111b;
}
.food .food-content .info .text {
      line-height: 24px;
      font-size: 12px;
      padding: 0 8px;
      color: #4d555d;
}
.food .food-content .rating {
    padding-top: 18px;
}
.food .food-content .rating .title {
      line-height: 14px;
      margin-left: 6px;
      font-size: 14px;
      color: #07111b;
}
.food .food-content .rating-wrapper {
    padding: 0 18px;
}
.food .food-content .rating-wrapper .rating-item {
      position: relative;
      padding: 16px 0;
      position: relative;
}
.food .food-content .rating-wrapper .rating-item:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        content: '';
}
.food .food-content .rating-wrapper .rating-item .user {
        position: absolute;
        right: 0;
        top: 16px;
        font-size: 0;
        line-height: 12px;
}
.food .food-content .rating-wrapper .rating-item .user .name {
          display: inline-block;
          margin-right: 6px;
          font-size: 10px;
          color: #93999f;
          vertical-align: top;
}
.food .food-content .rating-wrapper .rating-item .user .avatar {
          border-radius: 50%;
}
.food .food-content .rating-wrapper .rating-item .time {
        margin-bottom: 6px;
        line-height: 12px;
        font-size: 10px;
        color: #93999f;
}
.food .food-content .rating-wrapper .rating-item .text {
        line-height: 16px;
        font-size: 12px;
        color: #07111b;
}
.food .food-content .rating-wrapper .rating-item .text .icon-thumb_up, .food .food-content .rating-wrapper .rating-item .text icon-thumb_down {
          line-height: 16px;
          margin-right: 4px;
          font-size: 12px;
}
.food .food-content .rating-wrapper .rating-item .text .icon-thumb_up {
          color: #00a0dc;
}
.food .food-content .rating-wrapper .rating-item .text .icon-thumb_down {
          color: #93999f;
}
.food .food-content .rating-wrapper .no-rating {
      padding: 16px 0;
      font-size: 12px;
      color: #93999f;
}
.food.move-enter-active, .food.move-leave-active {
    transition: all .3s;
}
.food.move-enter, .food.move-leave-active {
    transform: translate3d(100%, 0, 0);
}



